.masthead {
  margin-bottom: 30px;

  @media screen and (min-width: $width-tablet) {
    margin-top: 20px;
  }

  &__logo {
    display: block;
    width: 80px;
    height: auto;
    margin: 20px 0;

    @media screen and (min-width: $width-tablet) {
      width: 300px;
    }
  }

  &__search {
    position: absolute;
    right: 4px;
    top: 10px;
    width: 44px;
    height: 44px;
    text-align: center;
    line-height: 44px;

    @media screen and (min-width: $width-tablet) {
      display: none;
    }
  }

  &__title {
    text-align: center;

    @media screen and (min-width: $width-tablet) {
      text-align: left;
    }
  }
}
